<template>
  <div id="format-json" class="mdui-row">
    <div>
      <div
        class="mdui-chip mdui-m-r-1"
        v-for="(item, index) in tabs"
        :key="index"
        :class="{ 'mdui-text-color-theme-accent': activeId === item.id }"
        @click="changeTab(item.id)"
      >
        <span class="mdui-chip-title">{{ item.name }}</span>
        <span class="mdui-chip-delete" @click.stop="deleteTab(index)">
          <i class="mdui-icon material-icons">cancel</i>
        </span>
      </div>
      <mdui-btn
        class="mdui-text-color-theme mdui-m-r-1"
        mdui-tooltip="{content: 'æ°å»º tab'}"
        @click="addTab"
        icon
        dense
        ripple
      >
        <mdui-icon type="add" />
      </mdui-btn>
      <mdui-btn
        class="mdui-text-color-red mdui-m-r-1"
        mdui-tooltip="{content: 'å é¤ææ tab'}"
        @click="clearTab"
        icon
        dense
        ripple
      >
        <mdui-icon type="delete" />
      </mdui-btn>
    </div>
    <div class="mdui-col-xs-6">
      <editor mode="application/json" v-model="jsonSource" @input="inputJson" />
    </div>
    <div class="mdui-col-xs-6">
      <vue-json-pretty
        :data="jsonObject"
        class="json-view mdui-m-t-1 mdui-shadow-2"
        showLength
        highlightMouseoverNode
      />
      <div class="mdui-m-t-1">
        <copy-btn :value="JSON.stringify(jsonObject, null, 2)" />
        <div class="mdui-btn-group">
          <mdui-btn @click="changeOrder('default')" dense ripple>é»è®¤</mdui-btn>
          <mdui-btn @click="changeOrder('asc')" dense ripple>ååº</mdui-btn>
          <mdui-btn @click="changeOrder('desc')" dense ripple>éåº</mdui-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import VueJsonPretty from 'vue-json-pretty'
import { isArray, isObject, isObjectLike, isPlainObject } from '@hhp1614/utils/lib/common/type'
import { each, sort } from '@hhp1614/utils/lib/common/collection'

export default {
  name: 'FormatJson',
  components: { VueJsonPretty },
  data() {
    return {
      // è¾å¥ç JSON
      jsonSource: '',
      // è¾åºçå¯¹è±¡
      jsonObject: {}
    }
  },
  computed: {
    ...mapState('format/json', ['tabs', 'activeId']),
    ...mapGetters(['cmTheme'])
  },
  created() {
    this.jsonSource = this.tabs.find(i => i.id === this.activeId)?.json ?? ''
    this.generateFromSource()
  },
  methods: {
    ...mapActions('format/json', ['acAddTab', 'acUpdateActiveTab', 'acUpdateTab', 'acClearTabs', 'acDeleteTab']),
    // äºä»¶ï¼è¾å¥ JSON
    inputJson() {
      this.acUpdateTab({ id: this.activeId, json: this.jsonSource })
      this.generateFromSource()
    },
    // æ ¹æ®è¾å¥ JSON çæå¯¹è±¡
    generateFromSource() {
      // è¾å¥ä¸ºç©º
      if (this.jsonSource.trim() === '') {
        this.jsonObject = {}
        this.showError(false)
        return
      }
      const json = this.parseJson(this.jsonSource)
      // è¾å¥ä¸æ¯æ°ç»æå¯¹è±¡
      if (!isObjectLike(json)) {
        this.showError()
        return
      }
      this.jsonObject = json
      this.showError(false)
    },
    // è§£æ JSON å­ç¬¦ä¸²
    parseJson(json) {
      try {
        return JSON.parse(json)
      } catch {
        return null
      }
    },
    // æ¾ç¤ºéè¯¯ä¿¡æ¯
    showError(show = true) {
      if (!show) return
      this.jsonObject = { error: 'ä¸æ¯ææç JSON å­ç¬¦ä¸²' }
    },
    // è®¾ç½®è¾å¥ç JSON
    setJsonSource(action, param) {
      action(param).then(() => {
        this.jsonSource = this.tabs.find(i => i.id === this.activeId)?.json ?? ''
        this.generateFromSource()
      })
    },
    // äºä»¶ï¼åæ¢ tab
    changeTab(id) {
      // ä¼å
      if (id === this.activeId) return
      this.setJsonSource(this.acUpdateActiveTab, id)
    },
    // äºä»¶ï¼å¢å  tab
    addTab() {
      this.setJsonSource(this.acAddTab)
    },
    // äºä»¶ï¼å é¤ tab
    deleteTab(index) {
      if (this.tabs.length <= 1) return
      this.setJsonSource(this.acDeleteTab, index)
    },
    // äºä»¶ï¼æ¸ç©º tab
    clearTab() {
      this.setJsonSource(this.acClearTabs)
    },
    // äºä»¶ï¼æåº default-é»è®¤ asc-ååº desc-éåº
    changeOrder(order) {
      // é»è®¤åé¡ºåºè¾åº
      if (order === 'default') {
        this.generateFromSource()
        return
      }
      this.jsonObject = this.sortJson(this.jsonObject, order)
    },
    // JSON æåº
    sortJson(json, order) {
      if (isPlainObject(json)) {
        json = { ...sort(json, order) }
      }
      if (isArray(json)) {
        json = [...json]
      }
      each(json, (value, key) => {
        if (isObject(value)) {
          // éå½æåº
          json[key] = this.sortJson(value, order)
        }
      })
      return json
    }
  }
}
</script>
